import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, App } from 'ionic-angular';
import { AppSoundProvider } from '../../providers/app-sound/app-sound';


@IonicPage()
@Component({
  selector: 'page-common-error-model',
  templateUrl: 'common-error-model.html',
})
export class CommonErrorModelPage {
  private erroInfo: any[];
  signup: boolean = false;
  constructor(private navParams: NavParams,
    private viewCtrl: ViewController, public appSound: AppSoundProvider,
    private navCtrl: NavController,
    public app: App) {
       
    this.erroInfo = this.navParams.data.errorInfo;
    var message = this.navParams.data.errorInfo.message;
    if (message == "new_msn") {
      this.signup = true;
    }

  }
  tryAgain() {
    this.appSound.play('buttonClick');
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad CommonErrorModelPage');
  }
  SignUp() {
       
    var data = "signup";
    this.appSound.play('buttonClick');
    this.viewCtrl.dismiss(data);
  }
}



